.tutorial {
  position: absolute;
  z-index: 3;
  background-color: rgba(255, 255, 255, 1);
  width: 800px;
  height: 800px;
  border: 2px solid #34495e;
  border-radius: 4px;
  text-align: center;
  float: right;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  padding: 15px;
  font-size: x-large;
  user-select: none;
  box-sizing: border-box;
}

.title {
  font-weight: bold;
}


@media only screen and (max-width: 1200px) {
  .tutorial {
    width: 600px;
    height: 600px;
    font-size: large;
  }
}
@media only screen and (max-width: 600px) {
  .tutorial {
    width: 400px;
    height: 400px;
    font-size: medium;
  }
}
@media only screen and (max-width: 425px) {
  .tutorial {
    width: 300px;
    height: 400px;
    font-size: small;
  }
}

.header-t {
  display: flex;
  width: 100%;
  height: 10%;
  align-items: center;
  justify-content: center;
}

.body-t {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 83%;
  overflow-y: auto;
  padding: 20px;
}

.body-t img {
  margin: 0 auto;
  margin-bottom: 20px;
}

.footer-t {
  display: flex;
  width: 100%;
  height:7%;
  justify-content: center;
  align-items: center;
}

#exit {
    position: absolute;
    right: 2%;
    top: 2%;
    cursor: pointer;
}

#exit:hover {
    color: rgb(90, 88, 88);
}

#back {
  position: absolute;
  left: 1%;
  bottom: 1%;
  cursor: pointer;
}

#back:hover {
  color: rgb(90, 88, 88);
}

#next {
  position: absolute;
  right: 1%;
  bottom: 1%;
  cursor: pointer;
}

#next:hover {
  color: rgb(90, 88, 88);
}

#pages {
  position: absolute;
  right: 47%;
  bottom: 2%;
  font-size: large;
  font-family: sans-serif;
  user-select: none;
}



